<template>
<div class="hello">
    <form action="">
        <div class="input-group mb-3">
            <input type="text" v-model="videoSearch" class="form-control" placeholder="Video URL" aria-label="Video URL" aria-describedby="basic-addon1">
            <div class="input-group-append">
                <button v-on:click="searchVideo" class="btn btn-outline-secondary" type="button">Search</button>
            </div>
        </div>
    </form>
    <div class="row">
        <div class="col-12 col-sm-6 col-md-4 col-lg-2" v-for="video in videoList" :key="video.title">
            <div class="card">
                <img class="card-img-top" :src="video.thumbnail_url" alt="Card image cap">
                <div class="card-body">
                    <div class="close-wrapper">
                        <div class="close"><i class="icon-remove"></i></div>
                    </div>
                    <p class="card-title cut-text">{{video.title}}</p>
                    <p class="card-text cut-text">{{video.description}}</p>
                    <div class="text-center">
                        <button class="btn btn-primary" type="button">Download</button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import * as ytdl from "ytdl-core";

export default {
    name: 'HelloWorld',
    props: {},
    data: function () {
        return {
            videoList: [],
            videoSearch: ""
        }
    },
    methods: {
        searchVideo: function () {
            if (this.videoSearch) {
                ytdl.getBasicInfo(this.videoSearch, (err, info) => {
                    if (err) {
                        console.error(err);
                    } else {
                        this.videoList.push(info);
                        console.log(info);
                    }
                });
            }
        }
    }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style scoped>
.close-wrapper {
    position: relative;
}
.close {
    position: absolute;
    color: red;
    right: 5px;
}
.cut-text {
    text-overflow: ellipsis;
    overflow: hidden; 
    height: 1.2em; 
    white-space: nowrap;
}
h3 {
    margin: 40px 0 0;
}

ul {
    list-style-type: none;
    padding: 0;
}

li {
    display: inline-block;
    margin: 0 10px;
}

a {
    color: #42b983;
}
</style>
